.project-card {
  width: 100%;
  height: 100%;
  display: flex;

  .project-svg {
    width: 50%;
    svg {
      width: 100%;
      height: 100%;
    }
  }
  .project-details {
    width: 50%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding-left: 30px;
    h1 {
      font-size: 8vw;
      font-family: "Antic Slab", serif;
      font-weight: normal;
    }
    h5 {
      align-self: flex-end;
      font-family: inherit;
      padding-right: 10px;
      font-size: 2.5vw;
    }
    p {
      font-family: inherit;
      font-size: 4vw;
      margin-bottom: 10%;
    }
    a {
      text-decoration: none;
      align-self: center;
      font-size: 2vw;
      color: inherit;
      font-family: inherit;
      position: absolute;
      bottom: 0;
      text-shadow: 3px 3px 5px #6c63ff;
      text-transform: uppercase;
      &:hover {
        color: rgba(#101010, 0.6);
        text-shadow: unset;
      }
    }
  }
}

// svg animations follows
#message-1 {
  animation: fall 4s linear both infinite;
}
@keyframes fall {
  from {
    transform: translateY(0);
  }
  50% {
    transform: translateY(100px);
  }
  to {
    transform: translateY(0);
  }
}

#message-2 {
  animation: upup 4s linear both infinite;
}
@keyframes upup {
  from {
    transform: translateY(100px);
  }
  50% {
    transform: translateY(-100px);
  }
  to {
    transform: translateY(100px);
  }
}

#bubble {
  animation: shake-bubble 0.8s cubic-bezier(0.455, 0.03, 0.515, 0.955) both
    infinite;
}
@keyframes shake-bubble {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

#hand {
  animation: write 1s linear both infinite;
}
@keyframes write {
  from {
    transfrom: translateX(-10px);
  }
  to {
    transform: translateX(10px);
  }
}

#shop-mens {
  animation: mens 5s linear both infinite;
}

@keyframes mens {
  from {
    transform: translateY(-50px);
  }

  to {
    transform: translateY(300px);
  }
}
#shop-womens {
  animation: womens 10s linear both infinite;
}

@keyframes womens {
  from {
    transform: translateY(-200px);
  }

  to {
    transform: translateY(20px);
  }
}
