.projects {
  display: flex;
  justify-content: center;
  align-items: center;
  .project {
    cursor: pointer;
    position: absolute;
    width: 80%;
    height: 80%;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    will-change: transform, opacity;
    text-shadow: 0px 2px 40px #00000020, 0px 2px 5px #00000030;
  }
}
