footer {
  height: 100%;
  display: flex;
  justify-content: center;
  column-gap: 20px;
  margin-top: 0;
  background: rgba(0, 0, 0, 0.8);
  a {
    svg {
      fill: #dfdbe5;
      width: 30px;
      &:hover {
        #github {
          fill: black;
        }
        #linkedin {
          fill: #0077b5;
        }
      }
    }
  }
  p {
    align-self: flex-end;
  }
}
