#react-js-svg {
  animation: rotate 10s linear infinite;
}

@keyframes rotate {
  to {
    transform: rotate(360deg);
  }
}

.projects-header {
  font-family: "Rochester", cursive;
  font-size: 3.6rem;
  color: rgba(#101010, 0.8);
  text-shadow: 2px 2px 2px #00d8ff;
}

.window {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  svg {
    width: 80%;
    height: 80%;
  }
}

#my-pic {
}
#picture-coming {
  animation: show-pic-coming 20s ease-in infinite;
}

@keyframes show-pic-coming {
  from {
    opacity: 0;
  }
  90% {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
