.message-me {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: 100%;
  cursor: pointer;
  #message-main {
    display: flex;
    width: 80%;
    height: 80%;
    transition: fill linear 3s;
    &:hover {
      * {
        animation: slow-fill 5s linear both;
      }
    }
  }
}
@keyframes slow-fill {
  to {
    fill: #101010;
  }
}
