.message-form {
  width: 80%;
  height: 80%;
  margin-top: 6%;
  margin-left: 10%;
  padding: 25px;
  box-shadow: 10px 10px rgba($color: #101010, $alpha: 0.8);
  background-color: rgba($color: #101010, $alpha: 0.7);
  @media (max-width: 560px) {
    // display: none;
  }
  h2 {
    text-align: center;
    text-transform: uppercase;

    img {
      width: 200px;
      height: 100px;
    }
  }
}

.thankYouForMessaging {
  text-align: center;
  font-family: "Rochester", cursive;
  font-size: 50px;
  width: auto;
  height: auto;
  position: absolute;
  color: black;
  top: 40%;
  left: 0%;
  margin: 0 200px;
  padding: 20px;
  font-weight: bold;
  background: transparent;
  box-shadow: 5px 5px 5px floralwhite;
  border: 2px solid #00d8ff;
  border-radius: 1%;

  @media (max-width: 560px) {
    // display: none;
  }
}
