%shrinkLabel {
  top: -20px;
  font-size: 15px;
  color: whitesmoke;
}

.group {
  position: relative;
  margin: 45px 0;

  .form-input {
    background: none;
    background-color: rgba($color: white, $alpha: 0.7);
    color: grey;
    font-size: 18px;
    padding: 10px 10px 10px 5px;
    display: block;
    width: 100%;
    border: none;
    border-radius: 0;
    border-bottom: 1px solid grey;
    margin: 25px 0;
    @media (min-device-width: 2800px) {
      margin: 50px 0;
      height: 100px;
      font-size: 40px;
    }

    &:focus {
      outline: none;
      color: #101010;
      box-shadow: 5px 5px 20px rgba($color: whitesmoke, $alpha: 0.8);
      background-color: rgba($color: white, $alpha: 0.82);
    }

    &:focus ~ .form-input-label {
      @extend %shrinkLabel;
    }
  }

  .form-input-label {
    color: black;
    font-size: 16px;
    font-weight: normal;
    position: absolute;
    pointer-events: none;
    left: 5px;
    top: 10px;
    transition: 300ms ease all;

    &.shrink {
      @extend %shrinkLabel;
    }
  }
}
